import React from 'react';
import Header from "../components/Custom/Header";
import Navigation from "./navigation";
import styles from "../sass/contact.module.scss"
import Footer from "../components/Custom/Footer";
import Container from "../components/Container";
import Title from "../components/Custom/Title";
import CustomButton from "../components/Custom/CustomButton";
import {useLocation} from "react-router-dom";
import {faLinkedinIn} from "@fortawesome/free-brands-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

const Contact = () => {
    const {state} = useLocation();

    return (
        <Container>
            <Header btn={(state != null) ? state.src : Navigation.Home}
                    nav={(state != null) ? `/${state.src}` : `/${Navigation.Home}`}/>

            <Title title={"Contact"}/>

            {/*TODO Form validation ??*/}
            <form className={styles.container}>

                <div className={styles.row}>
                    <input className={styles.name} placeholder={"Name"}/>
                    <input className={styles.email} type={"email"} placeholder={"Email"}/>
                </div>

                <input className={styles.subject} placeholder={"Subject"}/>

                <textarea className={styles.msg} placeholder={"Message"}/>

                <div className={styles.contactBtnContainer}>
                    <CustomButton text={"Send"} revers/>
                    <span className={styles.linkedInBtn}>
                        <FontAwesomeIcon icon={faLinkedinIn}/>
                        <a href={"https://www.linkedin.com/in/muhammad-sharafeldin-363393150/"} rel="noreferrer"
                           target={"_blank"}>
                            LinkedIn
                        </a>
                    </span>
                </div>

            </form>

            <Footer/>
        </Container>
    );
};

export default Contact;
